@import "../../global.scss";

.roadmap {
  background-color: #e1e7e9;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 50vw;

  h1 {
    padding-bottom: 40px;
    font-size: 50px;
    height: -webkit-fill-available;
  }
  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;
    background-color: #ed143dcf;
    border-radius: 50%;

    &.left {
      left: 100px;
      transform: rotateY(180deg);
    }

    &.right {
      right: 100px;
    }
  }

  .slider {
    height: 800px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 1000px;
        border-style: solid;
        background-repeat: no-repeat;
        background-size: 1000px;
        background-position: center;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        h4 {
          color: darkslategrey;
          position: absolute;
          top: 0;
          right: 0;
          padding: 5px;
          font-size: 10px;
          text-shadow: 1px 1px 1px #59c3c3a1;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer {
              width: 80px;
              height: 80px;
              border-radius: 50%;
              // background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;

              img {
                width: 100px;
                border-style: solid;
                border-width: 1px 0px 1px 0px;
              }
            }

            h2 {
              font-size: 20px;
            }

            p {
              font-size: 13px;
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }

        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          overflow: hidden;
          // img {
          //     width: 400px;
          // }
        }
      }
    }
  }
}

.roadmapmobile {
  .container {
    background-color: #e1e7e9;
    height: 80%;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: space-around;
    flex-direction: column;

    .card {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      box-shadow: 0px 0px 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      background-color: transparent;

      &.featured {
        width: 250px;
        height: 75%;
        margin: 0 30px;

        @include mobile {
          width: 250px;
          height: 180px;
          margin: 1px;
        }
      }

      &:hover {
        transform: scale(1.1);
        @include mobile {
          transform: none;
        }
      }

      .top {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column-reverse;

        img {
          &.user {
            height: 100%;
            width: 100%;
            object-fit: cover;
            margin: 0 30px;
          }
        }
      }
      .center {
        padding: 10px;
        border-radius: 10px;
        background-color: rgb(250, 244, 245);

        @include mobile {
          font-size: 12px;
          padding: 5px;
        }
      }
      .bottom {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h3 {
          margin-bottom: 5px;

          @include mobile {
            font-size: 12px;
          }
        }
        h4 {
          color: gray;
          @include mobile {
            font-size: 10px;
          }
        }
      }
    }
  }
}
